.loadingDots-wrapper div {
  position: absolute;
  width: 20px;
  height: 20px;
  box-sizing: content-box;
  background: #ffffff;
  border-radius: 50%;
  -webkit-transform: translate(40px, 40px) scale(1);
          transform: translate(40px, 40px) scale(1);
  -webkit-animation: loadingDots-wrapper 2.127659574468085s infinite
    cubic-bezier(0, 0.5, 0.5, 1);
